.square {
  background: #fff;
   border: 1px solid gray;
   float: left;
   margin-right: 5px;
   margin-top: 5px;
   padding: 0;
   width: 80px;
   height: 80px;
   text-align: center;
   font-size: 40px;
   font-weight: bold;
   line-height: 80px;
}

.square:focus {
  outline: none;
}
.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.game {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  width: 260px;
  margin: 0 auto;
}
ul {
  padding-left: 30px;
}
.game-info {
  margin-top: 20px
}
.status {
  margin-bottom: 10px;
}
